.navbar{
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 10px 5%;
}
.logo {
  color: #F2FDFF;
  text-decoration: none;
  font-size: 28px;
  font-weight: 700;
  position: relative;
}
.menu{
  display: inline-block;
  list-style: none;
  margin: 10px 10px;
  font-weight: 600;
}
.desktopMenuListItem{
  margin: 1rem;
  color: #F2FDFF;
  text-decoration: none;
  font-size: 24px;
  position: relative;
}
.desktopMenuListItem::after{
  content: '';
  width: 0;
  height: 3px;
  background: #11F6A3;
  position: absolute;
  left: 0;
  bottom: -6px;
  transition: 0.5s;
}
.desktopMenuListItem:hover::after{
  width: 100%;
}