.dev{
    display: block;
    width: 100%;
    max-width: 600px;
    padding:  10px 5%;
    justify-content: flex-start;
    
  }
  .next {
    position: relative;
    padding: 10px 5%;
}
.content {
    font-size: 26px;
    max-width: 800px;
    text-align: justify;
    font-style: italic;
    margin-bottom: 40px; 
    font-weight: lighter;
}
@media only screen and (max-width: 600px){
  .content{
    font-size: 26px;
  }
}


.name {
  padding-top: 40px;
  padding-bottom: 40px;
  width: 100%;
}

  .des{
    font-size: 96px;
    padding: 10px 0px;
    font-weight: 600;
    color: #11F6A3;
  }
  span{
    color: #F2FDFF;
  }
  @media only screen and (max-width: 600px){
    .des{
      font-size: 84px;
    }
  }
  .top-right {
    position: absolute;
    top: 0;
    right: 0;
    z-index: -1;
    opacity: 70%;
  }
  .bottom-left {
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: -1;
    opacity: 70%;
  }
  .grid {
    position: absolute;
    top: 50%; /* Position the top of the grid at 50% of the container height */
    left: 70%; /* Position the left of the grid at 50% of the container width */
    transform: translate(-50%, -50%); /* Center the grid horizontally and vertically */
    height: 80%; /* Adjust the height of the grid */
    width: 80%; /* Adjust the width of the grid */
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: -1;
  }
  
  .grid-svg {
    height: 80%;
    width: 80%;
    position: relative;
    z-index: 1;
  }
  
 
