section{
    padding: 100px 0;
}
.max-width{
    max-width: 1300px;
    padding: 0 80px;
    margin: auto;
}

.about .about-content,
.services .serv-content{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
}
section .title{
    position: relative;
    text-align: center;
    font-size: 40px;
    font-weight: 500;
    margin-bottom: 60px;
    padding-bottom: 20px;
}
section .title::before{
    content: "";
    position: absolute;
    bottom: 0px;
    left: 50%;
    width: 180px;
    height: 3px;
    transform: translateX(-50%);
}
section .title::after{
    position: absolute;
    bottom: -8px;
    left: 50%;
    font-size: 20px;
    color: #6e6e6e;
    padding: 0 5px;
    background: #fff;
    transform: translateX(-50%);
}

/* Global Styles */
.services {
    color: #fff;
    padding: 50px 0; /* Added padding for better spacing */
}

.max-width {
    max-width: 1200px; /* Limiting content width */
    margin: 0 auto; /* Centering content */
}

.title {
    color:#11F6A3;
    text-align: center;
    font-size: 54px;
    margin-bottom: 50px;
}
@media only screen and (max-width: 600px){
    .title{
      font-size: 40px;
    }
  }




.card {
    width:1200px; 
    background: #222;
    text-align: center;
    border-radius: 10px;
    padding: 30px;
    cursor: pointer;
    transition: all 0.3s ease;
    margin-bottom: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.card:hover {
    background: #333;
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
}

.box {
    transition: transform 0.3s ease;
}

.card:hover .box {
    transform: scale(1.05);
}

.text {
    font-size: 20px;
    font-weight: 700;
    margin-top: 15px;
    font-style: bold;
    padding: 10px;
}

